<template>
  <div class="view-container-uxdesign">
    <h1 hidden class="ux-design-heading ux-design-main-heading" aria-label="My UX/UI Design Portfolio Heading">UX/UI Design Portfolio</h1>
      <div class="section-container-uxdesign">
        <div class="section-header-uxdesign">
          <div class="uxdesign-header-slideshow-container">
            <div class="prev-selector-uxdesign" @click="prevSlide()">
              <a class="prev-uxdesign" @click="prevSlide()">&#8249;</a>
            </div>
            <div class="slideshow-container-uxdesign">
              <div class="slide-uxdesign fade">
                <img :src="imageSrc" v-bind:alt="altText">
              </div>
            </div>
            <div class="next-selector-uxdesign" @click="nextSlide()">
              <a class="next-uxdesign" @click="nextSlide()">&#8250;</a>
            </div>
          </div>
          <div class="dot-container-uxdesign">
            <div class="slide-selectors-uxdesign">
              <span class="dot-uxdesign" id="dot1UXDesign" @click="currentSlide(1)" :class="{ backgroundDotColor: activeDot === 1 }"></span>
              <span class="dot-uxdesign" id="dot2UXDesign" @click="currentSlide(2)" :class="{ backgroundDotColor: activeDot === 2 }"></span>
              <span class="dot-uxdesign" id="dot3UXDesign" @click="currentSlide(3)" :class="{ backgroundDotColor: activeDot === 3 }"></span>
            </div>
          </div>
        </div>
        <div class="portfolio-section-container-uxdesign">
          <div class="section-content-uxdesign" id="portfolioUXDesign">
            <h2 class="ux-design-heading" id="projectsHeadingUXDesign">PROJECTS</h2>
            <div class="projects-container-uxdesign">
              <div class="project-uxdesign">
                <h3>MOVIE TOWN</h3>
                <div class="project1 animation-left" aria-label="Movie Town Project Link">
                  <router-link class="link" @click="setActive(1)" to='/movietown'></router-link>
                </div>
                <div class="links-uxdesign">
                  <a v-bind:href="getPDFUrl(pdfArray[0])" target=_blank>View as PDF</a>
                </div>
              </div>
              <div class="project-uxdesign">
                <h3>CAREER ADVISOR</h3>
                <div class="project2 animation-right" aria-label="Career Advisor Project Link">
                  <router-link class="link" @click="setActive(1)" to='/careeradvisor'></router-link>
                </div>
                <div class="links-uxdesign">
                  <a v-bind:href="getPDFUrl(pdfArray[1])">View as PDF</a>
                </div>
              </div>
              <div class="project-uxdesign">
                <h3>CRYPTOS</h3>
                <div class="project3 animation-left" aria-label="Cryptos - Cryptocurrency App Project Link">
                   <router-link class="link" @click="setActive(1)" to='/cryptos'></router-link>
                </div>
                <div class="links-uxdesign">
                  <a v-bind:href="getPDFUrl(pdfArray[2])">View as PDF</a>
                </div>
              </div>
            </div>
          </div>
          <div class="about-section-container-uxdesign" id="about">
            <h2 class="ux-design-heading" id="aboutHeadingUXDesign">ABOUT</h2>
            <div class="about-container-uxdesign">
              <div class="about-section-uxdesign" id="aboutMeDescription">
                <div class="about-text-uxdesign">
                  <p> {{ aboutText }} </p>
                </div>
                <img :src="getBioUrl('Description.png')" alt="Cell Phone Picture Displaying A 3D Cube">
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>

export default {
  name: 'UXDesignView',
  data () {
    return {
      slides: [
        'hero-image-1.png',
        'hero-image-2.png',
        'hero-image-3.png'
      ],
      text: [
        'UX Ideation Image',
        'UX Responsive Design Image',
        'Apps For Social Good Image'
      ],
      pdfArray: [
        'Movie_Town.pdf',
        'Career_Advisor.pdf',
        'Cryptos.pdf'
      ],
      aboutText: 'Hello, my name is Chris. I am a designer, developer, and an avid technology enthusiast. I thoroughly enjoy the creative process that designing and coding provides and thinking inside and outside of the',
      slideIndex: 1,
      startNumber: 1,
      activeDot: 1,
      delay: 3000,
      imageSrc: '',
      altText: ''
    }
  },
  components: {},
  methods: {
    getImageUrl (fileName) {
      return require('@/assets/images/ux/' + fileName)
    },
    getBioUrl (fileName) {
      return require('@/assets/images/bio/' + fileName)
    },
    getPDFUrl (fileName) {
      return ('@/assets/pdf/' + fileName)
    },
    checkSlideValue () {
      if (this.slideIndex < 1) {
        this.slideIndex = this.slides.length
      }
      if (this.slideIndex > this.slides.length) {
        this.slideIndex = 1
      }
    },
    currentSlide (n) {
      this.slideIndex = n
      this.resetCarousel()
    },
    nextSlide () {
      this.slideIndex = this.slideIndex + 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    prevSlide () {
      this.slideIndex = this.slideIndex - 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    resetCarousel () {
      this.setCurrentSlide()
      this.stopSlideShowTimer()
    },
    setCurrentSlide () {
      this.imageSrc = this.getImageUrl(this.slides[this.slideIndex - 1])
      this.altText = this.text[this.slideIndex]
      this.activeDot = this.slideIndex
    },
    setupSlideShow () {
      this.updateSlides()
      if (this.startNumber === 1) {
        this.setCurrentSlide()
        this.startNumber = this.startNumber + 1
      }
    },
    showSlides () {
      if (this.slideIndex > this.slides.length) {
        this.slideIndex = 1
      }
      this.setCurrentSlide()
      this.startSlideShowTimer()
      this.slideIndex = this.slideIndex + 1
    },
    startSlideShowTimer () {
      this.slideShowTimer = setTimeout(this.showSlides, this.delay)
    },
    stopSlideShowTimer () {
      clearTimeout(this.slideShowTimer)
      this.startSlideShowTimer()
    }
  },
  mounted () {
    this.startSlideShowTimer()
  }
}
</script>
