<template>
 <div class="card">
    <div class="card-title">
      {{ codePen.name }}
    </div>
    <div class="card-body">
      <img class="card-image" :src="getImgUrl(codePen.image)" alt="Code pen example">
    </div>
    <div class="card-footer">
      <a :href="codePen.url">Try it on codePen</a>
    </div>
  </div>
</template>

<script>
export default {
  props: ['codePen', 'index'],
  methods: {
    getImgUrl (image) {
      return require('@/assets/images/codepens/' + image)
    }
  }
}
</script>
