<template>
  <main class="view-container-home">
    <div class="title-section">
      <img src='@/assets/images/backgrounds/webpage_title.png' alt="A Developer's Profile - Transforming Ideas into Reality" id="title">
    </div>
    <div class="controls-container">
      <div class="gui"></div>
    </div>
    <div :key="$route.fullPath" class="canvas-container" id="canvas"></div>
  </main>
</template>

<script>

import * as animation from '@/animation.js'

export default {
  name: 'HomeView',
  props: {
    currentView: Number
  },
  data: () => {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      turbidityValue: null
    }
  },
  methods: {
    loadScene () {
      const scene = animation.createScene(document.querySelector('#canvas'))
      scene.init()
      scene.addGui(document.querySelector('.gui'))
    }
  },
  mounted () {
    this.loadScene()
  }
}
</script>
