<template>
    <div v-cloak class="sphere-grid-container">
        <div class="sphere-lighting">
            <div class="sphere-background-container" id="canvas">
                <div
                  class="sphere"
                  :class="{
                    topLeftAnimation: sectionNumber === 1,
                    topMiddleAnimation: sectionNumber === 2,
                    topRightAnimation: sectionNumber === 3,
                    leftAnimation: sectionNumber === 4,
                    rightAnimation: sectionNumber === 5,
                    bottomLeftAnimation: sectionNumber === 6,
                    bottomMiddleAnimation: sectionNumber === 7,
                    bottomRightAnimation: sectionNumber === 8
                  }">
                    <div class="sphere-grid-top">
                        <div
                          id="topLeft"
                          @mouseover="sectionNumber = 1"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                        <div
                          id="topMiddle"
                          @mouseover="sectionNumber = 2"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                        <div
                          id="topRight"
                          @mouseover="sectionNumber = 3"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                    </div>
                    <div class="sphere-grid-middle">
                        <div
                          id="left"
                            @mouseover="sectionNumber = 4"
                            @mouseleave="sectionNumber = 0"
                        >
                        </div>
                        <div
                          id="right"
                          @mouseover="sectionNumber = 5"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                    </div>
                    <div class="sphere-grid-bottom">
                        <div
                          id="bottomLeft"
                          @mouseover="sectionNumber = 6"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                        <div
                          id="bottomMiddle"
                          @mouseover="sectionNumber = 7"
                          @mouseleave="sectionNumber = 0"
                        >
                        {{ this.sectionNumber }}
                        </div>
                        <div
                          id="bottomRight"
                          @mouseover="sectionNumber = 8"
                          @mouseleave="sectionNumber = 0"
                        >
                        </div>
                    </div>
                </div>
                <span class="shadow"></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      sectionNumber: 0
    }
  }
}
</script>

<style scoped>
  :root{
    --xPos: 0;
    --yPos: 0;
  }

  .sphere-grid-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .sphere-background-containerr {
    width: 50%;
    height: 100%;
    margin: 0 auto;
  }

  .sphere{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 50%;
    background-image: url("~@/assets/images/graphics/nightsky.png");
    margin: 2% auto;
   }

  .sphere-grid-top, .sphere-grid-middle, .sphere-grid-bottom{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 33%;
  }

  #topLeft, #topMiddle, #topRight{
    width: 33%;
    height: 100%;
  }

  #left, #right{
    width: 50%;
    height: 100%;
  }

  #bottomLeft, #bottomMiddle, #bottomRight{
    width: 33%;
    height: 100%;
  }

  .topLeftAnimation {
    --xPos: -5000px;
    --yPos: -5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .topMiddleAnimation{
    --xPos: 0px;
    --yPos: -5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .topRightAnimation{
    --xPos: 5000px;
    --yPos: -5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .leftAnimation{
    --xPos: -5000px;
    --yPos: 0px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .rightAnimation{
    --xPos: 5000px;
    --yPos: 0px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .bottomLeftAnimation{
    --xPos: -5000px;
    --yPos: 5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .bottomMiddleAnimation{
    --xPos: -0px;
    --yPos: 5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .bottomRightAnimation{
    --xPos: 5000px;
    --yPos: 5000px;
    animation: rotateBackgroundImage 6s linear infinite;
  }

  .shadow {
    display: block;
    width: 350px;
    height: 100px;
    border-radius: 50%;
    background: radial-gradient(50% 50%, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0) 60%);
    margin: 5% auto;
  }

  @keyframes rotateBackgroundImage{
    100%{
      background-position: var(--xPos) var(--yPos);
    }
  }

  @media all and (min-width: 20em) {
    .sphere {
      width: 125px;
      height: 125px;
    }
  }

  @media all and (min-width: 50em) {
    .sphere {
      width: 150px;
      height: 150px;
    }
  }

  @media all and (min-width: 64em) {
    .sphere {
      width: 200px;
      height: 200px;
    }
  }
  @media all and (min-width: 160em){
    .sphere{
      width: 250px;
      height: 250px;
    }
  }
</style>
