let slideIndex, slideShowTimer, imageLink, imageToDisplay, slides, links, dots

const delay = 3000

setupBigAd()

function setupBigAd () {
  setTimeout(loadValues, 1500)
}

async function loadValues () {
  dots = document.getElementsByClassName('dot')
  imageLink = document.getElementById('imageLink')
  slides = ['car-small.png', 'home-small.png', 'elephant-small.png']
  links = ['https://www.thebig.ca/car', 'https://www.thebig.ca/home-insurance', 'https://www.thebig.ca/erin-mills']
  imageToDisplay = document.getElementById('imageToDisplay')

  document.querySelector('#prevSlideBigAd').addEventListener('click', prevSlideBigAd)
  document.querySelector('#nextSlideBigAd').addEventListener('click', nextSlideBigAd)

  document.querySelector('#dot1BigAd').addEventListener('click', currentSlide(1))
  document.querySelector('#dot2BigAd').addEventListener('click', currentSlide(2))
  document.querySelector('#dot3BigAd').addEventListener('click', currentSlide(3))

  document.querySelector('#companyStatementTitle').innerHTML = 'When you think BIG, you save BIG!'
  document.querySelector('#companyStatementMessage').innerHTML = 'Our BIG promise is that we will get you the right coverage for the best price from our trusted insurance carriers for auto and home insurance, life insurance, business insurance, and more!'

  slideIndex = 1

  setupSlideShow()
}

function checkSlideValue () {
  if (slideIndex < 1) {
    slideIndex = slides.length
  }

  if (slideIndex > slides.length) {
    slideIndex = 1
  }
}

function currentSlide (n) {
  slideIndex = n
  resetCarousel()
}

function getBigImageUrl (image) {
  return require('@/assets/images/big/' + image)
}

function nextSlideBigAd () {
  slideIndex = slideIndex + 1
  checkSlideValue()
  resetCarousel()
}

function prevSlideBigAd () {
  slideIndex = slideIndex - 1
  checkSlideValue()
  resetCarousel()
}

function resetCarousel () {
  updateSlidesBigAd()
  setCurrentSlide()
  stopSlideShowTimer()
}

function setCurrentSlide () {
  imageToDisplay.src = getBigImageUrl(slides[slideIndex - 1])
  imageLink.href = links[slideIndex - 1]
  imageLink.target = '_blank'
  dots[slideIndex - 1].style.backgroundColor = '#717171'
}

function setupSlideShow () {
  updateSlidesBigAd()
  if (slideIndex === 1) {
    setCurrentSlide()
    slideIndex = slideIndex + 1
  }
  startSlideShowTimer()
}

function showSlidesBigAd () {
  updateSlidesBigAd()
  if (slideIndex > slides.length) {
    slideIndex = 1
  }
  setCurrentSlide()
  startSlideShowTimer()
  slideIndex++
}

function updateSlidesBigAd () {
  for (let i = 0; i < slides.length; i++) {
    dots[i].style.backgroundColor = '#000000'
  }
}

/* Slideshow Timer */
function startSlideShowTimer () {
  slideShowTimer = setTimeout(showSlidesBigAd, delay) // Change image every 2 seconds
}

function stopSlideShowTimer () {
  clearTimeout(slideShowTimer)
  startSlideShowTimer()
}
