<template>
  <div class="view-container-aboutme">
    <div class="aboutme-container">
      <h1 hidden>About Me</h1>
      <div class="before-after-container">
        <figure class="image-container" aria-label="Picture of me. Summary of picture text, Name, Chris Clarke, Location, Ontario Canada, Occupation, Developer, Status, Active">
          <div class="visible" id="visibleImage" :style="{ width: currentWidth + '%' }" aria-label="Summary, It all started for me many years ago while I was in grade school when I got the opportunity
                to attend a robot programming course hosted by a local school. This is where I learned the basics of computer programming. Ever since then I
                have been hooked. I have enjoyed programming in HTML, CSS, Javascript, JQuery, PHP, Python, C#, and more. I have also enjoyed programming PLC's.
                In addition, I also like to build and repair PC's, especiallygaming rigs. I started at a time when you had to use dip switches for overclocking while also trying not to fry the CPU to the motherboard.">
            <div class="bottom" v-if="showBottomLink">
              <a href="https://ca.linkedin.com/in/chris-clarke-29aa45142?trk=profile-badge" id="linkedinLink">More about me</a>
            </div>
          </div>
          <div class="hidden"></div>
        </figure>
        <input type="range" min="0" max="100" v-model="sliderValue" class="slider" id="slider" name="slider" v-on:input="revealImage()">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AboutView',
  data () {
    return {
      sliderValue: 100,
      showBottomLink: true,
      currentWidth: 100
    }
  },
  methods: {
    revealImage () {
      // console.log(this.sliderValue)
      if (this.sliderValue < 35) {
        this.showBottomLink = false
      } else {
        this.showBottomLink = true
      }
      this.currentWidth = this.sliderValue
    }
  }
}
</script>
