<template>
 <div v-cloak class="form-examples-container">
    <div class="form-title">
      {{ formExamples.name }}
    </div>
    <div v-cloak class="form-examples-slideshow-container">
      <div class="slideshow-form-examples">
        <div class="prev-slide-selector-form-examples" @click="prevSlideForm()">
          <a class="prev-slide-form" id="prevSlideForm" >&#8249;</a>
        </div>
        <div class="slide-form fade">
          <img :src="imageSrc" v-bind:alt="altText" id="imageToDisplay">
        </div>
        <div class="next-slide-selector-form-examples" @click="nextSlideForm()">
          <a class="next-slide-form" id="nextSlideForm">&#8250;</a>
        </div>
      </div>
      <div class="dot-container-form-examples">
        <div class="slide-selectors-form-examples">
          <span class="dot-form" id="dot1Form" @click="currentSlide(1)" :class="{ backgroundDotColor: activeDot === 1 }"></span>
          <span class="dot-form" id="dot2Form" @click="currentSlide(2)" :class="{ backgroundDotColor: activeDot === 2 }"></span>
          <span class="dot-form" id="dot3Form" @click="currentSlide(3)" :class="{ backgroundDotColor: activeDot === 3 }"></span>
          <span class="dot-form" id="dot4Form" @click="currentSlide(4)" :class="{ backgroundDotColor: activeDot === 4 }"></span>
        </div>
      </div>
    </div>
    <div class="form-examples-footer"></div>
  </div>
</template>

<script>
import Forms from '@/form-examples.json'

export default {
  data () {
    return {
      formExamples: Forms,
      slideIndex: 1,
      startNumber: 1,
      activeDot: 1,
      delay: 3000,
      imageSrc: '',
      altText: ''
    }
  },
  methods: {
    getImageUrl (fileName) {
      return require('@/assets/images/forms/' + fileName)
    },
    checkSlideValue () {
      if (this.slideIndex < 1) {
        this.slideIndex = this.formExamples.length
      }

      if (this.slideIndex > this.formExamples.length) {
        this.slideIndex = 1
      }
    },
    currentSlide (n) {
      this.slideIndex = n
      this.resetCarousel()
    },
    nextSlideForm () {
      this.slideIndex = this.slideIndex + 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    prevSlideForm () {
      this.slideIndex = this.slideIndex - 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    resetCarousel () {
      this.setCurrentSlide()
      this.stopSlideShowTimer()
    },
    setCurrentSlide () {
      this.imageSrc = this.getImageUrl(this.formExamples[this.slideIndex - 1].url)
      this.altText = this.formExamples[this.slideIndex - 1].alt
      this.activeDot = this.slideIndex
    },
    setupSlideShow () {
      if (this.slideIndex === 1) {
        this.setCurrentSlide()
        this.slideIndex = this.startNumber + 1
      }
    },
    showSlidesForms () {
      if (this.slideIndex > this.formExamples.length) {
        this.slideIndex = 1
      }
      this.setCurrentSlide()
      this.startSlideShowTimer()
      this.slideIndex = this.slideIndex + 1
    },
    startSlideShowTimer () {
      this.slideShowTimer = setTimeout(this.showSlidesForms, this.delay) // Change image every 2 seconds
    },
    stopSlideShowTimer () {
      clearTimeout(this.slideShowTimer)
      this.startSlideShowTimer()
    }
  },
  mounted () {
    this.startSlideShowTimer()
  }
}
</script>

<style scoped>
  * {
    box-sizing: border-box;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
  }

  :root {
    --heightMobile: 40vh;
  }

  .form-examples-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .slideshow-form-examples {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .prev-slide-selector-form-examples, .next-slide-selector-form-examples {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #b6b6b6;
  }

 .prev-slide-selector-form-examples:hover, .next-slide-selector-form-examples:hover {
   background-color: #717171;
 }

  .prev-slide-form, .next-slide-form {
    font-size: 2em;
    color: #ffffff;
    margin: 0 auto;
  }

  .slide-form{
    overflow: hidden;
    text-align: center;
  }

  .slide-form img {
    margin: 0 auto;
  }

  .slide-selectors-form-examples{
    text-align: center;
  }

  .dot-container-form-examples{
    width: 100%;
  }

  .dot-form {
    display: inline-block;
    background-color: #000000;
    transition: background-color 0.6s ease;
    cursor: pointer;
    margin-right: 15px;
    border-radius: 50%;
  }

  .dot-form:hover{
    background-color: #717171;
    cursor: pointer;
  }

  .backgroundDotColor {
    background-color: #717171;
  }

  .form-examples-footer {
    margin: 0 auto;
  }

  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }

  /*** END OF STYLING ***/

  /*** START OF MEDIA QUERIES ***/

/* 200 */

  @media all and (min-width: 15em) {
    .form-examples-slideshow-container{
      height: var(--heightMobile);
    }

    .prev-slide-selector-form-examples, .next-slide-selector-form-examples {
      width: 8vw;
      height: 30vh;
    }

    .slide-form {
      display: flex;
      flex-direction: row;
      width: 100%;
    }

    .slide-form img {
      width: 50%;
    }

    .dot-container-form-examples {
      display: flex;
      justify-content: center;
      width: 50%;
      height: 5vh;
      margin: 2% 0 2% 28%;
    }

    .dot-form {
      width: 0.8em;
      height: 0.8em;
    }

  }

  @media all and (min-width: 20em) {
  }

  @media all and (min-width: 25em) {
  }

  @media all and (min-width: 30em) {
  }

  @media all and (min-width: 50em) {
  }

  @media all and (min-width: 75em) {
    .prev-slide-selector-form-examples, .next-slide-selector-form-examples {
      width: 4vw;
      height: 30vh;
    }
  }

  @media all and (min-width: 160em) {
    .form-examples-container {
      margin-bottom: 5%;
    }

    .prev-slide-selector-form-examples, .next-slide-selector-form-examples {
      width: 2vw;
      height: 40vh;
      margin-top: 1%;
    }

    .slide-form img {
      width: 80%;
    }
  }

  /*** END OF MEDIA QUERIES ***/

  /*** ANIMATION ***/

  @-webkit-keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }

  @keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }

  /*** END OF ANIMATION ***/
</style>
