<template>
  <div class="big-ad-container">
    <div class="big-ad-header">
      <div class="company-logo-container" id="companyLogo">
        <a :href="homePageLink" target="_blank">
          <div class="links-to-website" :style="{ backgroundImage: 'url(' + getImageUrl(companyLogo) + ')' }"></div>
        </a>
      </div>
      <div class="hero-image-container-big" id="heroImage">
        <a :href="profileLink" target="_blank">
          <div class="links-to-website" :style="{ backgroundImage: 'url(' + getImageUrl(profilePicture) + ')' }"></div>
        </a>
      </div>
    </div>
    <div class="company-statement-container">
      <h1 id="companyStatementTitle"> {{ companyTitle }} </h1>
      <p id="companyStatementMessage"> {{ companyStatement }} </p>
    </div>
    <div v-cloak class="slideshow-container-big">
      <div class="slideshow-big">
        <div class="prev-slide-selector-big">
          <a class="prev-slide-big" id="prevSlideBigAd" @click="prevSlideBigAd()">&#8249;</a>
        </div>
        <div class="slide-big fade">
          <a v-bind:href="link" id="imageLink" target="_blank"><img :src="imageSrc" v-bind:alt="altText" id="imageToDisplay"></a>
        </div>
        <div class="next-slide-selector-big">
          <a class="next-slide-big" id="nextSlideBigAd" @click="nextSlideBigAd()">&#8250;</a>
        </div>
      </div>
      <div class="dot-container-big-ad">
        <div class="slide-selectors-big-ad">
          <span class="dot-big-ad" id="dot1BigAd" @click="currentSlide(1)" :class="{ backgroundDotColor: activeDot === 1 }"></span>
          <span class="dot-big-ad" id="dot2BigAd" @click="currentSlide(2)" :class="{ backgroundDotColor: activeDot === 2 }"></span>
          <span class="dot-big-ad" id="dot3BigAd" @click="currentSlide(3)" :class="{ backgroundDotColor: activeDot === 3 }"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      slides: [
        'car-small.png',
        'home-small.png',
        'elephant-small.png'
      ],
      links: [
        'https://www.thebig.ca/car',
        'https://www.thebig.ca/home-insurance',
        'https://www.thebig.ca/erin-mills'
      ],
      text: [
        'Big Car Insurance Picture',
        'Big Home Imsurance Picture',
        'Big Elephant Picture'
      ],
      companyLogo: 'big-logo.png',
      profilePicture: 'jason.png',
      homePageLink: 'https://www.thebig.ca/erin-mills/',
      profileLink: 'https://www.thebig.ca/jason/',
      companyTitle: 'When you think BIG, you save BIG!',
      companyStatement: 'Our BIG promise is that we will get you the right coverage for the best price from our trusted insurance carriers for auto & home insurance, life insurance, business insurance, and more!',
      slideIndex: 1,
      startNumber: 1,
      activeDot: 1,
      delay: 3000,
      imageSrc: '',
      altText: '',
      link: ''
    }
  },
  methods: {
    getImageUrl (fileName) {
      return require('@/assets/images/big/' + fileName)
    },
    checkSlideValue () {
      if (this.slideIndex < 1) {
        this.slideIndex = this.slides.length
      }

      if (this.slideIndex > this.slides.length) {
        this.slideIndex = 1
      }
    },
    currentSlide (n) {
      this.slideIndex = n
      this.resetCarousel()
    },
    nextSlideBigAd () {
      this.slideIndex = this.slideIndex + 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    prevSlideBigAd () {
      this.slideIndex = this.slideIndex - 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    resetCarousel () {
      this.setCurrentSlide()
      this.stopSlideShowTimer()
    },
    setCurrentSlide () {
      this.imageSrc = this.getImageUrl(this.slides[this.slideIndex - 1])
      this.altText = this.text[this.slideIndex]
      this.link = this.links[this.slideIndex - 1]
      this.activeDot = this.slideIndex
    },
    setupSlideShow () {
      if (this.slideIndex === 1) {
        this.setCurrentSlide()
        this.slideIndex = this.startNumber + 1
      }
    },
    showSlidesBigAd () {
      if (this.slideIndex > this.slides.length) {
        this.slideIndex = 1
      }
      this.setCurrentSlide()
      this.startSlideShowTimer()
      this.slideIndex = this.slideIndex + 1
    },
    startSlideShowTimer () {
      this.slideShowTimer = setTimeout(this.showSlidesBigAd, this.delay) // Change image every 2 seconds
    },
    stopSlideShowTimer () {
      clearTimeout(this.slideShowTimer)
      this.startSlideShowTimer()
    }
  },
  mounted () {
    this.startSlideShowTimer()
  }
}
</script>

