        <li><router-link to='/about' @click="setBanner(false)">ABOUT</router-link></li>
        <li><router-link to="/portfolio" @click="setBanner(true); togglePortfolioDropDown()" @mouseover="togglePortfolioDropDown">PORTFOLIO <i>&#9660;</i></router-link>
          <div v-if="showPortfolioDropDown" class="dropDown">
            <ul class="dropDownMenu">
              <li><router-link :to="{ path: '/portfolio', hash: '#marketingSection' }" @click="togglePortfolioDropDown()">Marketing</router-link></li>
              <li><router-link :to="{ path: '/portfolio', hash: '#animationsSection' }" @click="togglePortfolioDropDown()">Animation</router-link></li>
              <li><router-link :to="{ path: '/portfolio', hash: '#codePensSection' }" @click="togglePortfolioDropDown()">CodePen Examples</router-link></li>
              <li><router-link :to="{ path: '/portfolio', hash: '#formsSection' }" @click="togglePortfolioDropDown()">Forms</router-link></li>
              <li><router-link :to="{ path: '/portfolio', hash: '#tablesSection' }" @click="togglePortfolioDropDown()">Tables</router-link></li>
              <li><router-link :to="{ path: '/portfolio', hash: '#extrasSection' }" @click="togglePortfolioDropDown()">Extras</router-link></li>
             </ul>
          </div>
        </li>
        <li><router-link to='/uxdesign' @click="setBanner(false); toggleUXDropDown()" @mouseover="toggleUXDropDown">UX/UI DESIGN <i>&#9660;</i></router-link>
