import { createRouter, createWebHistory } from 'vue-router'
import PortfolioView from '@/views/PortfolioView.vue'
import HomeView from '../views/HomeView.vue'
import AboutView from '@/views/AboutView.vue'
import UXDesignView from '@/views/UXDesignView.vue'
import ContactView from '@/views/ContactView.vue'
import MovieTownView from '@/views/MovieTownView.vue'
import CareerAdvisorView from '@/views/CareerAdvisorView.vue'
import CryptosView from '@/views/CryptosView.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'About',
    component: AboutView
  },
  {
    path: '/portfolio',
    name: 'Portfolio',
    component: PortfolioView
  },
  {
    path: '/uxdesign',
    name: 'UXDesign',
    component: UXDesignView
  },
  {
    path: '/contact',
    name: 'Contact',
    component: ContactView
  },
  {
    path: '/movietown',
    name: 'MovieTown',
    component: MovieTownView
  },
  {
    path: '/careeradvisor',
    name: 'CareerAdvisor',
    component: CareerAdvisorView
  },
  {
    path: '/cryptos',
    name: 'Cryptos',
    component: CryptosView
  }
]
// el: to.hash,
// behavior: 'smooth',
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior: function (to, from, savedPosition) {
    if (to.hash) {
      const element = document.getElementById(to.hash.replace(/#/, ''))
      if (element && element.scrollIntoView) {
        element.scrollIntoView({ block: 'start', behavior: 'smooth' })
      }
    }
  }
})

export default router
