<template>
 <div class="table-examples-container">
    <div class="table-title">
      {{ tableExamples.name }}
    </div>
    <div v-cloak class="table-examples-slideshow-container">
      <div class="slideshow-table-examples">
        <div class="prev-slide-selector-table-examples">
          <a class="prev-slide-table" id="prevSlidetable" @click="prevSlideTable()">&#8249;</a>
        </div>
        <div class="slide-table fade">
          <img :src="imageSrc" v-bind:alt="altText" id="imageToDisplay">
        </div>
        <div class="next-slide-selector-table-examples" @click="nextSlideTable()">
          <a class="next-slide-table" id="nextSlidetable">&#8250;</a>
        </div>
      </div>
      <div class="dot-container-table-examples">
        <div class="slide-selectors-table-examples">
          <span class="dot-table" id="dot1table" @click="currentSlide(1)" :class="{ backgroundDotColor: activeDot === 1 }"></span>
          <span class="dot-table" id="dot2table" @click="currentSlide(2)" :class="{ backgroundDotColor: activeDot === 2 }"></span>
        </div>
      </div>
    </div>
    <div class="table-examples-footer">
      <a :href="linkToCodePen">Try it on codePen</a>
    </div>
  </div>
</template>

<script>
import Tables from '@/table-examples.json'

export default {
  data () {
    return {
      tableExamples: Tables,
      linkToCodePen: 'https://codepen.io/01000011-01001010-01000011/pen/QWQwbzZ',
      slideIndex: 1,
      startNumber: 1,
      activeDot: 1,
      delay: 3000,
      imageSrc: '',
      altText: ''
    }
  },
  methods: {
    getImageUrl (fileName) {
      return require('@/assets/images/tables/' + fileName)
    },
    checkSlideValue () {
      if (this.slideIndex < 1) {
        this.slideIndex = this.tableExamples.length
      }

      if (this.slideIndex > this.tableExamples.length) {
        this.slideIndex = 1
      }
    },
    currentSlide (n) {
      this.slideIndex = n
      this.resetCarousel()
    },
    nextSlideTable () {
      this.slideIndex = this.slideIndex + 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    prevSlideTable () {
      this.slideIndex = this.slideIndex - 1
      this.checkSlideValue()
      this.resetCarousel()
    },
    resetCarousel () {
      this.setCurrentSlide()
      this.stopSlideShowTimer()
    },
    setCurrentSlide () {
      this.imageSrc = this.getImageUrl(this.tableExamples[this.slideIndex - 1].url)
      this.altText = this.tableExamples[this.slideIndex - 1].alt
      this.activeDot = this.slideIndex
    },
    setupSlideShow () {
      if (this.slideIndex === 1) {
        this.setCurrentSlide()
        this.slideIndex = this.startNumber + 1
      }
    },
    showSlidesTables () {
      if (this.slideIndex > this.tableExamples.length) {
        this.slideIndex = 1
      }
      this.setCurrentSlide()
      this.startSlideShowTimer()
      this.slideIndex = this.slideIndex + 1
    },
    startSlideShowTimer () {
      this.slideShowTimer = setTimeout(this.showSlidesTables, this.delay) // Change image every 2 seconds
    },
    stopSlideShowTimer () {
      clearTimeout(this.slideShowTimer)
      this.startSlideShowTimer()
    }
  },
  mounted () {
    this.startSlideShowTimer()
  }
}
</script>

<style scoped>
  * {
    box-sizing: border-box;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
  }

  :root {
    --heightMobile: 40vh;
  }

  .table-examples-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .slideshow-table-examples {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .prev-slide-selector-table-examples, .next-slide-selector-table-examples {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #b6b6b6;
  }

  .prev-slide-selector-table-examples:hover, .next-slide-selector-table-examples:hover {
    background-color: #717171;
  }

  .prev-slide-table, .next-slide-table {
    font-size: 2em;
    color: #ffffff;
    margin: 0 auto;
  }

  .slide-table{
    overflow: hidden;
    text-align: center;
  }

  .slide-table img{
    border: 2px solid rgb(109, 108, 108);
    margin: 0 auto;
  }

  .slide-selectors-table-examples{
    text-align: center;
  }

  .dot-container-table-examples{
    width: 100%;
  }

  .dot-table {
    display: inline-block;
    background-color: #000000;
    transition: background-color 0.6s ease;
    cursor: pointer;
    margin-right: 15px;
    border-radius: 50%;
  }

  .dot-table:hover{
    background-color: #717171;
    cursor: pointer;
  }

  .table-examples-footer {
    width: 100%;
    text-align: center;
  }

  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }

  /*** END OF STYLING ***/

  /*** START OF MEDIA QUERIES ***/

  /* 200 */
  @media all and (min-width: 15em) {
    .table-examples-container {
      margin-bottom: 10%;
    }

    .table-examples-slideshow-container{
      width: 100%;
      height: var(--heightMobile);
    }
    .prev-slide-selector-table-examples, .next-slide-selector-table-examples {
      width: 20vw;
      height: 40vh;
    }

    .slide-table {
      display: flex;
      flex-direction: row;
      margin: 0 0 0 0;
      padding: 0 0 0 0;
    }

    .slide-table img {
      width: 80%;
    }

    .dot-container-table-examples {
      display: flex;
      justify-content: center;
      width: 80%;
      height: 5vh;
      margin: 5% 0 0 12.5%
    }

    .dot-table {
      width: 0.8em;
      height: 0.8em;
    }

    .backgroundDotColor {
      background-color: #717171;
    }
  }

  @media all and (min-width: 20em) {
  }

  @media all and (min-width: 25em) {
  }

  @media all and (min-width: 30em) {
  }

  @media all and (min-width: 50em) {

    .prev-slide-selector-table-examples, .next-slide-selector-table-examples {
      width: 8vw;
    }

  }

  @media all and (min-width: 75em) {
    .prev-slide-selector-table-examples, .next-slide-selector-table-examples {
      width: 5vw;
      height: 40vh;
    }
  }

  @media all and (min-width: 160em) {
    .slideshow-table-examples {
      margin-left: 1.5%;
    }

    .prev-slide-selector-table-examples, .next-slide-selector-table-examples {
      width: 2vw;
      height: 40vh;
    }
    .dot-container-table-examples {
      width: 35%;
      margin: 5% 0 0 35%;
    }
    .dot-table{
      font-size: 1.5em;
    }
  }
 </style>
