<template>
    <div class="project-section-header">
        <div class="project-title">
            <h1 id="projectMainTitle"> {{ projectMainTitle }} </h1>
        </div>
        <div class="project-hero-image">
          <img :src="getImageUrl(imageFileName)" :alt="imageAltDescription">
        </div>
    </div>
</template>

<script>
export default {
  props: {
    projectMainTitle: String,
    imageFile: String,
    imageAlt: String
  },
  data () {
    return {
      projectTitle: this.projectMainTitle,
      imageFileName: this.imageFile,
      imageAltDescription: this.imageAlt
    }
  },
  methods: {
    getImageUrl (filename) {
      return require('@/' + filename)
    }
  }
}
</script>
