<template>
    <aside class="sideNavBar-container">
        <div class="sideNavBar">
            <span v-on:click="setToggle(false)" class="closeBtn">&times;</span>
            <router-link  @click="setToggle(false), setBanner(false), setIconColor('#ffffff')" to='/'>HOME</router-link>
            <router-link  @click="setToggle(false), setBanner(false), setIconColor('#ffffff') " to='/about'>ABOUT</router-link>
            <router-link  @click="setToggle(false), setBanner(true), setIconColor('#ffffff')" to='/portfolio'>PORTFOLIO</router-link>
            <router-link  @click="setToggle(false), setBanner(false), setIconColor('#000000')" to='/uxdesign'>UX/UI DESIGN</router-link>
            <router-link  @click="setToggle(false), setBanner(false), setIconColor('#ffffff')" to='/contact'>CONTACT</router-link>
        </div>
    </aside>
</template>
<script>
export default {
  props: {
    toggle: Boolean
  },
  methods: {
    setToggle (boolToggle) {
      this.$emit('setToggle', boolToggle)
    },
    setBanner (boolBanner) {
      this.$emit('setBanner', boolBanner)
    },
    setIconColor (color) {
      this.$emit('setIconColor', color)
    }
  }
}
</script>
