import * as THREE from 'three'

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

import { Sky } from 'three/examples/jsm/objects/Sky.js'

import { Water } from 'three/examples/jsm/objects/Water.js'

import * as dat from 'dat.gui'

export function createScene (container) {
  const canvas = {}

  canvas.cube = getCube()

  canvas.sky = getSky()

  canvas.init = function init () {
    canvas.container = container

    canvas.camera = getCamera()

    canvas.cubeSettings = {
      position_x: -5,
      position_y: 125,
      position_z: 50,
      rotation_x: 0.25,
      rotation_z: 0.25
    }

    /* WATER */
    canvas.waterGeometry = new THREE.PlaneGeometry(30000, 30000)

    /* SUN */
    canvas.sunSettings = {
      elevation: 1.5,
      azimuth: 180,
      exposure: 1
    }

    canvas.scene = new THREE.Scene()
    canvas.renderer = new THREE.WebGLRenderer({ antialias: true })
    setRenderer()
    canvas.pmremGenerator = new THREE.PMREMGenerator(canvas.renderer)
    // camera = this.getCamera()
    canvas.scene.add(canvas.cube)
    canvas.water = getWater()
    canvas.scene.add(canvas.water)
    canvas.sun = getSun()
    canvas.scene.add(canvas.sky)
    canvas.updateSun()
    canvas.controls = new OrbitControls(canvas.camera, canvas.renderer.domElement)
    canvas.setControls()
    // canvas.addGui()
    canvas.container.appendChild(canvas.renderer.domElement)
    animate()
  }

  function setRenderer () {
    canvas.renderer.setPixelRatio(devicePixelRatio)
    canvas.renderer.setSize(window.innerWidth, window.innerHeight)
    canvas.renderer.toneMapping = THREE.ACESFilmicToneMapping
  }

  function getCamera () {
    const newCamera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 1, 10000)

    newCamera.position.set(-5, 100, 800)

    return newCamera
  }

  function getCube () {
    let cubeGeometry

    if (window.innerWidth < 900) {
      cubeGeometry = new THREE.BoxGeometry(30, 30, 30)
    } else {
      cubeGeometry = new THREE.BoxGeometry(80, 80, 80)
    }

    const cubeImages = [
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@//assets/images/mobo.png')), side: THREE.DoubleSide
      }),
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@//assets/images/mobo.png')), side: THREE.DoubleSide
      }),
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@/assets/images/heading.png')), side: THREE.DoubleSide
      }),
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@/assets/images/heading.png')), side: THREE.DoubleSide
      }),
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@/assets/images/splash.png')), side: THREE.DoubleSide
      }),
      new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load(require('@/assets/images/htmlCodeImage.png')), side: THREE.DoubleSide
      })
    ]

    cubeGeometry = new THREE.Mesh(cubeGeometry, cubeImages)

    return cubeGeometry
  }

  function getSun () {
    const newSun = new THREE.Vector3()

    return newSun
  }

  function getWater () {
    canvas.scene.fog = new THREE.FogExp2(0xA9A9A9, 0.001)
    const newWater = new Water(
      canvas.waterGeometry, {
        textureWidth: 1024,
        textureHeight: 1024,
        waterNormals: new THREE.TextureLoader().load(require('@/assets/images/water-normals-texture.jpeg'),
          function (texture) {
            texture.wrapS = texture.wrapT = THREE.RepeatWrapping
          }),
        sunDirection: new THREE.Vector3(),
        sunColor: 0xffffff,
        waterColor: 0x000033,
        distortionScale: 3.7,
        fog: canvas.scene.fog.color
      }
    )

    newWater.rotation.x = -Math.PI / 2

    return newWater
  }

  function getSky () {
    const newSky = new Sky()
    newSky.scale.setScalar(45000)

    return newSky
  }

  canvas.setUniforms = function setUniforms () {
    canvas.skyUniforms.turbidity.value = 5
    canvas.skyUniforms.rayleigh.value = 1.345
    canvas.skyUniforms.mieCoefficient.value = 0.035
    canvas.skyUniforms.mieDirectionalG.value = 0.95
  }

  function animate () {
    requestAnimationFrame(animate)
    const time = performance.now() * 0.001

    /* CUBE */
    canvas.cube.position.x = canvas.cubeSettings.position_x
    canvas.cube.position.y = canvas.cubeSettings.position_y
    canvas.cube.position.z = canvas.cubeSettings.position_z
    canvas.cube.rotation.x = time * canvas.cubeSettings.rotation_x
    canvas.cube.rotation.z = time * canvas.cubeSettings.rotation_z
    /* WATER */
    canvas.water.material.uniforms.time.value += 2 / 60

    canvas.renderer.render(canvas.scene, canvas.camera)
  }

  canvas.setControls = function setControls () {
    canvas.controls.maxPolarAngle = Math.PI * 0.95
    canvas.controls.target.set(0, 85, 0)
    canvas.controls.minDistance = 135
    canvas.controls.maxDistance = 1500.0
    canvas.controls.update()
  }

  canvas.updateSun = function updateSun () {
    canvas.skyUniforms = canvas.sky.material.uniforms
    // this.setUniforms()
    const phi = THREE.MathUtils.degToRad(90 - canvas.sunSettings.elevation)
    const theta = THREE.MathUtils.degToRad(canvas.sunSettings.azimuth)

    canvas.sun.setFromSphericalCoords(1, phi, theta)
    canvas.sky.material.uniforms.sunPosition.value.copy(canvas.sun)
    canvas.water.material.uniforms.sunDirection.value.copy(canvas.sun).normalize()
    canvas.renderer.toneMappingExposure = canvas.sunSettings.exposure
    canvas.scene.environment = canvas.pmremGenerator.fromScene(canvas.sky).texture
  }

  canvas.addGui = function addGui (guiContainer) {
    canvas.guiContainer = guiContainer
    canvas.gui = new dat.GUI(
      { autoPlace: false }
    )

    canvas.folder = canvas.gui.addFolder('Sky Settings')
    canvas.folder.add(canvas.skyUniforms.turbidity, 'value').min(0).max(20).name('Turbidity')
    canvas.folder.add(canvas.skyUniforms.rayleigh, 'value').min(0).max(4).name('Rayleigh')
    canvas.folder.add(canvas.skyUniforms.mieCoefficient, 'value').min(0).max(0.1).name('MieCo')
    canvas.folder.add(canvas.skyUniforms.mieDirectionalG, 'value').min(0).max(1).name('MieDir')

    canvas.folder.add(canvas.sunSettings, 'elevation', 0, 90, 1).name('Elevation').onChange(canvas.updateSun)
    canvas.folder.add(canvas.sunSettings, 'azimuth', -180, 180, 1).name('Azimuth').onChange(canvas.updateSun)
    canvas.folder.add(canvas.sunSettings, 'exposure', 0, 1, 0.1).name('Exposure').onChange(canvas.updateSun)

    canvas.folder.close()

    canvas.folder2 = canvas.gui.addFolder('Cube Settings')

    canvas.folder2.add(canvas.cubeSettings, 'position_x', -1000, 1000, 1).name('Position X Axis')
    canvas.folder2.add(canvas.cubeSettings, 'position_y', 50, 500, 1).name('Position Y Axis')
    canvas.folder2.add(canvas.cubeSettings, 'position_z', -1000, 1000, 1).name('Position Z Axis')
    canvas.folder2.add(canvas.cubeSettings, 'rotation_x', 0.25, 1, 0.1).name('Rotation X')
    canvas.folder2.add(canvas.cubeSettings, 'rotation_z', 0.25, 1, 0.1).name('Rotation Y')

    canvas.folder2.close()

    canvas.gui.__ul.childNodes[0].classList.add('gui-list')
    canvas.gui.__ul.childNodes[1].classList.add('gui-list')
    canvas.gui.close()

    canvas.guiContainer.appendChild(canvas.gui.domElement)

    return container
  }

  canvas.getSkyValues = function getSkyValues () {
    const skyValues = [
      { name: 'turbidity', value: canvas.skyUniforms.turbidity },
      { name: 'rayleigh', value: canvas.skyUniforms.rayleigh },
      { name: 'mieco', value: canvas.skyUniforms.mieCoefficient },
      { name: 'miedir', value: canvas.skyUniforms.mieDirectionalG },
      { name: 'elevation', value: canvas.sunSettings.elevation },
      { name: 'azimuth', value: canvas.sunSettings.azimuth },
      { name: 'exposure', value: canvas.sunSettings.exposure }
    ]

    return skyValues
  }

  canvas.getCubeValues = function getCubeValues () {
    const cubeValues = [
      { name: 'xAxis', value: canvas.cubeSettings.position_x },
      { name: 'yAxis', value: canvas.cubeSettings.position_y },
      { name: 'zAxis', value: canvas.cubeSettings.position_z },
      { name: 'rotationX', value: canvas.cubeSettings.rotation_x },
      { name: 'rotationYZ', value: canvas.cubeSettings.rotation_z }
    ]

    return cubeValues
  }
  return canvas
}
