<template>
  <div class="angryball-container" id="angryBallAnimation">
    <div class="screen" :class="{ smash: isActive }" id="screen"></div>
    <div class="angryball-ball" :class="{ bounceAnimation: isActive }" id="angryball"></div>
  </div>
  <div class="user-instructions">
    <span v-if="isVisible" id="resetAngryball" @click="resetAnimation">Click to reset animation</span>
  </div>
</template>

<script>

export default {
  data () {
    return {
      isActive: true,
      isVisible: false
    }
  },
  methods: {
    showInstructions () {
      setTimeout(this.setValues, 11000)
    },
    setValues () {
      this.isVisible = true
    },
    resetAnimation () {
      this.isVisible = false
      this.isActive = false
      setTimeout(() => {
        this.isActive = true
        this.showInstructions()
      }, 500)
    }
  },
  mounted () {
    this.showInstructions()
  }
}
</script>

<style scoped>
.angryball-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("~@/assets/images/graphics/angry-ball/background.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  perspective: 1000px;
  transform-style: preserve-3d;
  overflow: hidden;
  margin: 5% auto;
}

.screen {
  visibility: hidden;
  position: absolute;
  top: 0;
  width: 100%;
  margin-top: -25%;
  z-index: 2;
}

.smash {
  animation-name: smashScreen;
  animation-duration: 1s;
  animation-delay: 10.65s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}

.angryball-ball {
  position: absolute;
  background-color: #ffffff;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  z-index: 1;
}

.bounceAnimation {
  animation: bounce 2s linear 4, linear 4, shake 0.5s 8s linear 5, charge 0.1s 10.5s linear 1;
}

.user-instructions {
  display: block;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: blue;
  text-align: center;
  margin: -50% 0 0 0;
}

.user-instructions:hover {
  cursor: pointer;
  text-decoration: underline;
}

/* START OF ANIMATION */
@keyframes bounce{
  0% {
    transform: translateY(-68px) skewX(-30deg) scale(0.95);
  }

  50% {
    transform: translateY(calc(100vh * 0.35));
  }

  55% {
    transform: translateY(calc(100vh * 0.30)) scaleX(1.25);
  }

  100% {
    transform: translateY(0vh) scaleX(1);
  }
}

@keyframes shake{
  10%{
    transform: rotateZ(-20deg) scale(1.25);
  }

  20%{
    transform: translateY(10%) translateX(-5%) rotateZ(20deg) scale(1.25);
  }

  30%{
    transform: translateY(30%) translateX(5%) rotateZ(-20deg) scale(1.5);
  }

  40%{
    transform: translateY(var(--zoon1)) translateX(-5%) rotateZ(20deg) scale(1.25);
  }

  50%{
    transform: translateY(30%) translateX(5%) rotateZ(-20deg) scale(1.5);
  }

  60%{
    transform: translateY(10%) translateX(-5%) rotateZ(20deg) scale(1.25);
  }

  70%{
    transform: translateY(30%) translateX(5%) rotateZ(-20deg) scale(1.5);
  }

  80%{
    transform: translateY(10%) translateX(-5%) rotateZ(20deg) scale(1.25);
  }

  90%{
    transform: translateY(30%) translateX(5%) rotateZ(-20deg) scale(1.5);
  }

  100%{
    transform: translateY(10%) translateX(-5%) rotateZ(20deg) scale(1.25);
  }
}

@keyframes charge {
  0% {
    transform: translateY(-50%) scale(1.5);
  }

  100% {
    transform: translateY(-100%) scale(5);
  }
}

@keyframes smashScreen {
  100% {
    visibility: visible;
  }
}

/* END OF ANIMATION */

/* START OF MEDIA QUERIES */

/* 280 */
@media all and (min-width: 15em) {
  .angryball-container {
    width: 100vw;
    height: 95vh;
    background-size: cover;
  }

  .angryball-ball{
    width: 41px;
    height: 45px;
    background-image: url("~@/assets/images/graphics/angry-ball/angryballmicro.png");
    margin: -18% auto;
  }

  .screen {
    height: 120vh;
    background-image: url("~@/assets/images/graphics/angry-ball/shatteredsmall.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-top: -35%;
  }

  .user-instructions{
    height: 5vh;
    margin-top: 5%;
  }
}

/* 500 */
@media screen and (min-width: 30em) {
  .angryball-ball {
    width: 81px;
    height: 89px;
    background-image: url("~@/assets/images/graphics/angry-ball/angryballsmall.png");
  }

  .screen {
    background-image: url("~@/assets/images/graphics/angry-ball/shatteredsmall.png");
    margin-top: -30%;
  }
}

/* 900 */
@media all and (min-height: 50em) {
  .angryball-container {
    height: 70vh;
  }
  .screen{
    height: 100vh;
  }
}

/* 1024 */
@media screen and (min-width: 64em) {
  .angryball-container {
    width: 60vw;
    height: 80vh;
  }
  .screen {
    height: 130%;
    background-image: url("~@/assets/images/graphics/angry-ball/shattered.png");
    margin-top: -10%;
  }

  .user-instructions p {
    font-size: 1.25em;
  }
}

/* 1920 */
@media screen and (min-width: 120em) {
  .angryball-container {
     width: 40vw;
  }
  .angryball-ball{
    width: 126px;
    height: 138px;
    background-image: url("~@/assets/images/graphics/angry-ball/angryball.png");
    margin-top: -18% auto;
  }

  .screen {
    margin-top: -20%;
  }
}

/* 2560 */
@media screen and (min-width: 160em) {
  .angryball-container {
    width: 40vw;
    background-size: cover;
    background-position: center;
  }

  .user-instructions {
    font-size: 1.5em;
    margin-top: 5%;
    z-index: 10;
  }
}
</style>
