<template>
  <div class="sideBarNavMenu">
    <span id="sideNavIcon" @click="toggleMenu(true)">
      <span :style="{ color: iconColor}">&#9776;</span>
    </span>
  </div>
  <div v-if="showBanner" class="banner"></div>
  <MainHeader
    :showBanner="showBanner"
    @setBanner="setBanner($event)"
  />
  <SideNavBar
    v-if="toggle"
    :toggle="toggle"
    @setToggle="toggleMenu($event)"
    :showBanner="showBanner"
    @setBanner="setBanner($event)"
    :iconColor="iconColor"
    @setIconColor="setIconColor($event)"
  />
  <router-view :key="$route.fullPath"/>
  <!-- :codePenExamples="codePenExamples"  -->
</template>

<script>
import MainHeader from '@/components/MainHeader.vue'
import SideNavBar from '@/components/SideNavBar.vue'

export default {
  name: 'App',
  components: {
    MainHeader,
    SideNavBar
  },
  data () {
    return {
      toggle: false,
      showBanner: this.checkShowBanner(),
      iconColor: this.checkIconColor()
    }
  },
  methods: {
    toggleMenu (boolToggle) {
      this.toggle = boolToggle
    },
    checkShowBanner () {
      if (!sessionStorage.getItem('showBanner')) {
        this.showBanner = false
      } else {
        this.showBanner = sessionStorage.getItem('showBanner')
      }
    },
    checkIconColor () {
      if (!sessionStorage.getItem('iconColor')) {
        this.iconColor = '#ffffff'
      } else {
        this.showBanner = sessionStorage.getItem('iconColor')
      }
    },
    setBanner (value) {
      this.showBanner = value
      sessionStorage.setItem('showBanner', value)
    },
    setIconColor (color) {
      this.iconColor = color
      sessionStorage.setItem('iconColor', color)
    }
  }
}
</script>
