<template>
  <div v-cloak class="book-container">
    <div class="book">
      <div class="pages">
        <div class="backOfPrevPage" id="backPage" v-bind:style="{ backgroundImage: 'url(' + backImageUrl + ')' }">
          <div v-if="showIndicator" class="turnPageIndicator" id="back" @click="turnToPreviousPage()"></div>
        </div>
        <div class="frontOfNextPage" id="frontPage" v-bind:style="{ backgroundImage: 'url(' + frontImageUrl + ')' }">
          <div  v-if="showIndicator" class="turnPageIndicator" id="forward" @click="turnToNextPage()"></div>
        </div>
      </div>
      <div class="page" id="bookPages" v-bind:style="{ transform: pagesTransform }" >
       <div class="page1" id="leftPage" v-bind:style="{ backgroundImage: 'url(' + page1ImageUrl + ')', transform: page1Transform }"></div>
        <div class="page2" id="rightPage" v-bind:style=" { backgroundImage: 'url(' + page2ImageUrl + ')', transform: page2Transform }"></div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      imageArrayLeft: ['tableLeft', 'backCoverLeft', 'emptyPageLeft', 'page2', 'page4', 'emptyPageLeft', 'backCoverLeft'],
      imageArrayRight: ['frontCover', 'titlePage', 'page1', 'page3', 'emptyPageRight', 'backCoverRight', 'tableRight'],
      backImageUrl: 'none',
      frontImageUrl: 'none',
      page1ImageUrl: 'none',
      page2ImageUrl: 'none',
      elementBorder: '',
      pagesTransform: '',
      page1Transform: '',
      page2Transform: '',
      dirToTurn: 'forward',
      imageSize: 'Medium.png',
      prevPageCounter: 0,
      nextPageCounter: 0,
      degOfRot: 18,
      showIndicator: true
    }
  },
  methods: {
    getImgURL (image) {
      return require('@/assets/images/graphics/book-covers/' + image)
    },
    clearBorder () {
      this.elementBorder = 'none'
    },
    loadStartingImages () {
      /* Load the starting images into book */
      this.backImageUrl = this.getImgURL('tableLeft' + this.imageSize)
      this.page1ImageUrl = this.getImgURL(this.imageArrayLeft[0] + this.imageSize)
      this.page2ImageUrl = this.getImgURL(this.imageArrayRight[0] + this.imageSize)
    },
    loadValues () {
      if (window.innerWidth < 700) {
        this.imageSize = 'Micro.png'
      }

      if (window.innerWidth >= 700) {
        this.imageSize = 'Small.png'
      }

      if (window.innerWidth > 1599) {
        this.imageSize = 'Medium.png'
      }
      this.prevPageCounter = 0
      this.nextPageCounter = 0
    },
    resetBook () {
      this.backImageUrl = this.getImgURL(this.imageArrayLeft[0] + this.imageSize)
      this.frontImageUrl = this.getImgURL(this.imageArrayRight[0] + this.imageSize)
      this.loadStartingImages()
    },
    setBorder () {
      switch (this.nextPageCounter) {
        case 0:
          this.elementBorder = '2px solid rgb(70, 20, 0)'
          break
        case 1:
        case 2:
        case 3:
        case 4:
          this.elementBorder = '2px solid rgb(214, 214, 214)'
          break
        case 5:
          this.elementBorder = '2px solid rgb(70, 20, 0)'
          break
      }
    },
    turnPage (value) {
      setTimeout(() => {
        if (this.dirToTurn === 'forward') {
          this.pagesTransform = 'rotateY(-' + this.degOfRot + 'deg)'
        }

        if (this.dirToTurn === 'back') {
          this.pagesTransform = 'rotateY(' + this.degOfRot + 'deg)'
        }

        if (value > -1) {
          if (this.dirToTurn === 'forward') {
            this.page1ImageUrl = this.getImgURL('blankPageWhite' + this.imageSize)
          }

          if (this.dirToTurn === 'back') {
            this.page2ImageUrl = this.getImgURL('blankPageWhite' + this.imageSize)
          }
        }

        if (value === 0) {
          if (this.dirToTurn === 'back') {
            this.page1Transform = 'scaleX(1)'
          }
        }

        if (value === 2) {
          if (this.dirToTurn === 'forward') {
            this.frontImageUrl = this.getImgURL(this.imageArrayRight[this.nextPageCounter] + this.imageSize)
          }

          if (this.dirToTurn === 'back') {
            this.backImageUrl = this.getImgURL(this.imageArrayLeft[this.nextPageCounter] + this.imageSize)
            this.page1Transform = 'scaleX(1)'
          }
        }

        if (value === 5) {
          if (this.dirToTurn === 'forward') {
            this.page2ImageUrl = this.getImgURL(this.imageArrayLeft[this.nextPageCounter] + this.imageSize)
            this.page2Transform = 'scaleX(-1)'
          }

          if (this.dirToTurn === 'back') {
            this.page1ImageUrl = this.getImgURL(this.imageArrayRight[this.nextPageCounter] + this.imageSize)
            this.page1Transform = 'scaleX(-1)'
          }
        }

        if (value === 9) {
          if (this.dirToTurn === 'forward') {
            this.backImageUrl = this.getImgURL(this.imageArrayLeft[this.nextPageCounter] + this.imageSize)
            this.page2Transform = 'translateZ(0)'
          }

          if (this.dirToTurn === 'back') {
            this.frontImageUrl = this.getImgURL(this.imageArrayRight[this.nextPageCounter] + this.imageSize)
            this.backImageUrl = this.getImgURL(this.imageArrayLeft[this.nextPageCounter] + this.imageSize)
          }
          this.showIndicator = true
        }
        this.degOfRot = this.degOfRot + 18
      }, 100 * value)
    },
    turnToNextPage () {
      this.setBorder()
      this.nextPageCounter = this.nextPageCounter + 1
      // console.log('NEXT PAGE COUNTER: ' + this.nextPageCounter)
      if (this.nextPageCounter > this.imageArrayRight.length - 1) {
        this.nextPageCounter = 0
        this.resetBook()
      } else {
        this.degOfRot = 18
        this.dirToTurn = 'forward'
        const imageNum = this.nextPageCounter - 1
        this.showIndicator = false
        this.page2ImageUrl = this.getImgURL(this.imageArrayRight[imageNum] + this.imageSize)

        for (let i = 0; i < 10; i++) {
          this.turnPage(i)
        }
      }
    },
    turnToPreviousPage () {
      this.clearBorder()
      if (this.nextPageCounter > 0) {
        this.nextPageCounter = this.nextPageCounter - 1
        this.degOfRot = 18
        this.dirToTurn = 'back'
        const imageNum = this.nextPageCounter
        this.page1ImageUrl = this.imageArrayLeft[imageNum] + this.imageSize

        for (let i = 0; i < 10; i++) {
          this.turnPage(i)
        }
      }
    }
  },
  mounted () {
    // this.loadImageArrays()
    this.loadStartingImages()
  }
}
</script>
